import React, { ReactElement, useMemo, useCallback, useReducer, useState, useEffect, useContext } from 'react';
import { TournamentStatus, Player, TournamentSettings, Bonus } from '../../interfaces';
import PlayerData from '../../models/Player';
import Players from './Players';

import styles from '../../css/crosstable.module.css';
import { GET, server } from '../../utils/requests';
import url from 'url';
import { TieBreaks } from '../../resources/tiebreaks';
import { TiebreakContext } from '../../Contexts';

export interface TableProps {
	setDisplayPlayer: (id: string) => void
	show: () => void
	status: TournamentStatus
	players: Player[]
	settings: TournamentSettings
	round: number
}

interface ColumnData {
	id?: (entry: PlayerData, i: number) => string
	name: string | ((key: string, i: number) => string)
	className: 'blank' | 'playerName' | 'locked' | 'rating' | 'unset' | 'edit' | ((v: string, i: number, settings: TournamentSettings) => string),
	sort?: false | ((a: any, b: any) => number),
	show?: (entry: PlayerData, i: number, settings: TournamentSettings) => boolean
	convert?: (v: any, p: PlayerData, s: TournamentSettings) => string
	postConvert?: (v: string, i: number) => string
	mobile?: string
}

export default function Standings(props: TableProps): ReactElement {

	const players = Players(props);

	const columns = useMemo(() => {
		return {
			pre: {
				rank: {
					mobile: 'Rank',
					name: '',
					className: 'blank',
					sort: false,
					postConvert: (v: string, i: number): string => (1 + i).toString()
				},
				name: {
					name: 'Name',
					className: 'playerName',
					id: (p: PlayerData): string => p.id,
					sort: (a: string, b: string) => {
						if (a > b) return 1;
						if (a < b) return -1;
						return 0;
					},
				},
				rating: {
					name: 'Rating',
					className: 'locked',
					show: (e: PlayerData, i: number, s: TournamentSettings) => s.showRatings
				}
			},
			roundColumn: {
				name: (key: string, i: number): string => key.slice(-1),
				className: (v: string, i: number, s: TournamentSettings) => {
					if (v === '*') return 'edit';
					if (v === '') return 'unset';
					else return 'locked';
				},
				convert: (v: string, p: PlayerData, s: TournamentSettings) => {
					if (v === '') return '';
					else if (v === '*') return '*';
					let nb = parseFloat(v);
					let output: string;
					if (nb === 0.5) output = '';
					else output = nb.toString();
					if (v.endsWith('b')) output += 'b';
					return output;
				},
				sort: (a: string, b: string): number => {
					if (a === b) return 0;
					if (a === '') return 1;
					else if (b === '') return -1;
					if (a === '*') return 1;
					else if (b === '*') return -1;
					let [aN, bN] = [parseInt(a), parseInt(b)];
					if (a.toString().endsWith('b')) aN -= 0.001;
					if (b.toString().endsWith('b')) bN -= 0.001;
					return bN - aN;
				}
			},
			post: {
				score: {
					mobile: 'Match Points',
					name: 'MP',
					className: 'locked',
					convert: (v: number, p: PlayerData): string => Math.floor(v) === v ? v.toFixed() : v === 0.5 ? '' : Math.floor(v).toFixed() + ''
				},
				gamePoints: {
					mobile: 'Game Points',
					name: 'GP',
					className: 'locked',
					convert: (v: number): string => v.toFixed(1)
				},
				gameDifference: {
					mobile: 'Game Point Difference',
					name: 'G. +/-',
					className: 'locked',
					convert: (v: number): string => {
						if (!v) return '';
						if (v > 0) return '+' + v.toFixed(1);
						else return v.toFixed(1)
					},
				},
				gamesPlayed: {
					mobile: 'Games Played',
					name: 'P',
					className: 'locked',
					convert: (v: number): string => v.toString()
				},
				matchPercentage: {
					mobile: 'Match Point Percentage',
					name: 'M. %',
					className: 'rating',
					convert: (v: number): string => v.toFixed()
				},
				gamePercentage: {
					mobile: 'Game Point Percentage',
					name: 'G. %',
					className: 'rating',
					convert: (v: number): string => v.toFixed()
				},
				performanceRating: {
					mobile: 'Performance Rating',
					name: 'Perf',
					className: 'rating',
					convert: (v: number): string => v.toFixed(),
					show: (entry: PlayerData, i: number, s: TournamentSettings) => s.showRatings
				},
				ratingDifference: {
					mobile: 'Performance Rating Difference',
					name: 'P. +/-',
					className: 'rating',
					convert: (v: number): string => {
						if (!v) return '';
						if (v > 0) return '+' + v.toString();
						else return v.toString()
					},
					show: (entry: PlayerData, i: number, s: TournamentSettings) => s.showRatings
				},
				expectedScore: {
					mobile: 'Expected Score',
					name: 'Expected',
					className: 'rating',
					convert: (v: number, p: PlayerData): string => v.toFixed(1) + ' / ' + p.played,
					show: (entry: PlayerData, i: number, s: TournamentSettings) => s.showRatings
				},
				expectedDifference: {
					mobile: 'Score to Expected',
					name: 'E. +/-',
					className: 'rating',
					convert: (v: number): string => {
						if (typeof v === 'undefined') return '';
						if (v > 0) return '+' + v.toFixed(2);
						else return v.toFixed(2)
					},
					show: (entry: PlayerData, i: number, s: TournamentSettings) => s.showRatings
				},
				wins: {
					mobile: 'Wins',
					name: 'W',
					className: 'locked',
					convert: (v: number): string => v.toString()
				},
				draws: {
					mobile: 'Draws',
					name: 'D',
					className: 'locked',
					convert: (v: number): string => v.toString()
				},
				losses: {
					mobile: 'Losses',
					name: 'L',
					className: 'locked',
					convert: (v: number): string => v.toString()
				},
				played: {
					mobile: 'Played',
					name: 'P',
					className: 'locked',
					convert: (v: number): string => v.toString()
				}
			}
		} as {
			pre: {
				[key: string]: ColumnData
			},
			roundColumn: ColumnData
			post: {
				[key: string]: ColumnData
			}

		}
	}, [props.round]);

	const totalRounds = useMemo((): number => {
		let totalRounds: number;
		if (props.status.round > props.settings.totalRounds) totalRounds = props.status.round;
		else if (props.settings.totalRounds === 'Infinity') totalRounds = props.status.round;
		else totalRounds = props.settings.totalRounds;
		return totalRounds;
	}, [props.status.round, props.settings.totalRounds]);

	const roundHeaders = useMemo(() => {
		let h = [] as string[];
		for (let i = 1; i <= totalRounds; i++) {
			h.push('round.' + i.toString());
		}
		return h;
	}, [totalRounds]);

	const headers: string[] = useMemo(() => [...Object.keys(columns.pre), ...roundHeaders, ...Object.keys(columns.post)], [roundHeaders])
		.filter((key, i) => {
			let meta = columns.pre[key] || columns.post[key];
			if (!meta) return true;
			if (meta.show && !meta.show(null as any as PlayerData, i, props.settings)) return false;
			return true;
		});

	const setSortHeader = useCallback((state: [string, number], action: string): [string, number] => {
		if (!headers.includes(action)) return ['', 0];
		if (state[0] !== action) return [action, 0];
		else return [action, state[1] + 1];
	}, [headers]);
	const [sort, sortBy] = useReducer(setSortHeader, ['score', 0]);

	const tiebreaks = useContext(TiebreakContext);

	const renderRows: JSX.Element[] = useMemo(() => {
		if (!players.length) return [];
		let rowsData = [] as [number, ColumnData, PlayerData][][];
		for (let i = 0; i < players.length; i++) {
			let p = players[i];
			if (p.id === 'bye') continue;
			let pres = [] as [number, ColumnData, PlayerData][];
			for (let [k, meta] of Object.entries(columns.pre)) {
				if (meta.show && !meta.show(p, i, props.settings)) continue;
				pres.push([p[k as keyof Player] as number, meta, p]);
			}
			let _rounds = p.rounds
			for (let j = _rounds.length + 1; j <= totalRounds; j++) _rounds.push('')
			let rounds = _rounds.map(entry => [entry as any as number, columns.roundColumn, p]) as [number, ColumnData, PlayerData][];
			let post = [] as [number, ColumnData, PlayerData][];
			for (let [k, meta] of Object.entries(columns.post)) {
				if (meta.show && !meta.show(p, i, props.settings)) continue;
				post.push([p[k as keyof Player] as number, meta, p]);
			}
			let rowData = [...pres, ...rounds, ...post];
			rowsData.push(rowData);
		}

		let sortFunc = (a: number, b: number): number => b - a;
		let sortMeta = sort[0].startsWith('round') ? columns.roundColumn : (columns.pre[sort[0]] || columns.post[sort[0]]);
		if (sortMeta && sortMeta.sort !== false) {
			if (sortMeta.sort) sortFunc = sortMeta.sort;
			let sortCol = headers.indexOf(sort[0]);
			if (sortCol !== -1)
			rowsData = rowsData.sort((a, b): number => {
				let x: number;
				if (sort[1] % 2) x = sortFunc(b[sortCol][0], a[sortCol][0]);
				else x = sortFunc(a[sortCol][0], b[sortCol][0]);
				if (x) return x;
				let allTb = tiebreaks.slice(0);
				let useTb = allTb.shift();
				while (useTb) {
					let res = useTb[1].func(a[sortCol][2], b[sortCol][2]);
					if (res) return res;
					useTb = allTb.shift();
				}
				return 0;
			});
		}

		let rows = rowsData.map((rowData, i) => {
			let x = (
				<tr key={'row.' + i} className={styles.tr}>
					{rowData.map(([value, meta, p]: [number, ColumnData, PlayerData], j: number) => {
						let display = value ? value.toString() : '';
						if (meta.convert) display = meta.convert(value, p, props.settings);
						else if (meta.postConvert) display = meta.postConvert(display, i);
						if (typeof display === 'undefined' || display === '') display = '\u200b';
						let className = typeof meta.className === 'function' ? meta.className(display, j, props.settings) : meta.className;
						return <>
							<th className={[styles.cell, styles.headerCell].join(' ')} key={['cellHeader', j].join('.')}>
								<div>
									{meta.mobile ? meta.mobile : typeof meta.name === 'function' ? meta.name((headers[j] || '').toString(), j) : meta.name}
								</div>
							</th>
							<th key={['cellBody', j].join('.')}>							
								<div
									id={meta.id ? meta.id(p, j) : undefined}
									key={[value, j].join('.')}
									className={[styles.cell, className, styles[className]].join(' ')}
								>
									{display}
								</div>
							</th>
						</>;
					})}
				</tr>
			);
			return x;
		})
		return rows;
	}, [players, sort]);
		
	return (
		<section>
			<table className={['download-table full-width', styles.table, 'standings'].join(' ')}>
				<thead className={styles.headers}>
					<tr>
						{headers.map((key, i) => {
							let meta = key.startsWith('round') ? columns.roundColumn : (columns.pre[key] || columns.post[key]);
							return <th
								scope='column'
								key={['column', i].join('.')}
								className={[styles.th, key.startsWith('round') ? styles.round : ''].join(' ')}
								id={key}
								onClick={() => sortBy(key)}
							>
								<div className={[styles.cell, styles.headerCell].join(' ')}>
									{typeof meta.name === 'function' ? meta.name(key, i) : meta.name}
								</div>
							</th>;
						})}
					</tr>
				</thead>
				<tbody>						
					{renderRows}
				</tbody>
			</table>
		</section>
	);
}